import iziToast from 'izitoast';
import { COLORS, TOAST } from './constants.js';

// --- –ë–∞–∑–æ–≤–∞ –∫–æ–Ω—Ñ—ñ–≥—É—Ä–∞—Ü—ñ—è ---
const defaultOptions = {
  position: TOAST.POSITION.TOP_RIGHT,
  timeout: TOAST.TIMEOUT.DEFAULT,
  close: true,
  closeOnEscape: true,
  pauseOnHover: true,
  progressBar: true,
  progressBarColor: COLORS.GRAY_LIGHT,
  transitionIn: TOAST.TRANSITION.IN,
  transitionOut: TOAST.TRANSITION.OUT,
  fontFamily: 'inherit',
  icon: '',
};

// 1. –ü–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è –ø—Ä–æ –£–°–ü–Ü–• (Success)
export const showSuccess = message => {
  iziToast.show({
    ...defaultOptions,
    title: TOAST.TITLE.SUCCESS,
    message: message,
    backgroundColor: COLORS.BLACK,
    titleColor: COLORS.WHITE,
    messageColor: COLORS.WHITE,
    icon: TOAST.ICON.SUCCESS,
    iconColor: COLORS.WHITE,
  });
};

// 2. –ü–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è –ø—Ä–æ –ü–û–ú–ò–õ–ö–£ (Error)
export const showError = message => {
  iziToast.show({
    ...defaultOptions,
    title: TOAST.TITLE.ERROR,
    message: message,
    backgroundColor: COLORS.BLACK,
    titleColor: COLORS.WHITE,
    messageColor: COLORS.GRAY_LIGHT,
    progressBarColor: COLORS.WHITE,
    icon: TOAST.ICON.ERROR,
    iconColor: COLORS.WHITE,
  });
};

// 3. –Ü–Ω—Ñ–æ—Ä–º–∞—Ü—ñ–π–Ω–µ –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è (Info)
export const showInfo = message => {
  iziToast.show({
    ...defaultOptions,
    title: TOAST.TITLE.INFO,
    message: message,
    backgroundColor: COLORS.WHITE,
    titleColor: COLORS.BLACK,
    messageColor: COLORS.BLACK,
    progressBarColor: COLORS.BLACK,
    borderBottom: `2px solid ${COLORS.BLACK}`,
  });
};
